.toggler-container {
  margin: 20px 270px 20px 270px;

  .filter-Icon {
    width: 22px;
    cursor: pointer;
  }

  .sort-Icon {
    display: none;
  }

  .selection-filter-container {
    display: inline-flex;
    gap: 8px;
    margin-left: 10%;
  }

  .selection-filter {
    display: flex;
    gap: 10px;
    align-items: center;
    border-radius: 4px;
    font-family: "Mulish", "open Sans", sans-serif;
    font-size: 1.2rem;
    padding: 12px;
    background-color: rgba(128, 128, 128, 0.065);
    cursor: pointer;

    &:hover {
      background-color: #000000d7;
      color: white;
      transition: 0.5s background-color;
    }
  }

  .selection-clear {
    font-size: 1.2rem;
    font-family: "Mulish", "open Sans", sans-serif;
    color: rgba(0, 0, 0, 0.832);
    font-weight: 800;
    margin-left: 10px;
    transition: 0.2s color;

    &:hover {
      color: rgba(0, 0, 0, 0.7);
    }
  }
}

.text-container {
  display: flex;
  justify-content: space-between;
}

.productLayout {
  display: flex;
  margin-left: 260px;
  margin-right: 260px;
  margin-top: 0.7%;
}

span {
  font-family: "Mulish", sans-serif;
  font-size: 1.4rem;
  cursor: pointer;
  margin-top: 2%;
}

.productlayout-container {
  display: flex;
  width: 100%;
}

.product-layout-container {
  &.disabled {
    pointer-events: none;
    opacity: 0.5;
  }
}

.sidebar-container {
  width: 14%;
  background-color: rgba(128, 128, 128, 0.076);
  position: sticky;
  top: 0;
  height: 100vh;
  transition: width 0.3s ease;
}

.side-container input[type="checkbox"] {
  width: 15px;
  height: 15px;
}

/* Optional: Style the label for better aesthetics */
.checkbox-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-family: "Mulish", sans-serif;
  color: rgb(0, 0, 0);
  margin-bottom: 3px;
}

.side-page- p {
  font-size: 1.3rem;
  margin: 0;
  padding-bottom: 14px;
  font-family: Arial, Helvetica, sans-serif;
  color: #000000a9;
}

.product-details-container {
  display: flex;
  flex-direction: column;
  gap: 7px;
  padding: 5px;
}

.product-salesprice-container {
  display: flex;
  align-items: center;
  gap: 10px;

  .product-salesprice {
    font-family: "Mulish", sans-serif;
    color: #c51e28;
    font-size: 1.2rem;
    font-weight: 600;

    &.cart-info {
      color: #22242a;
      font-weight: 400;
      font-size: 1.3rem;
    }
  }

  .product-salesprice-value {
    font-family: "Mullish", sans-serif;
    font-weight: 800;
    color: #c51e28;
    font-size: 1.5rem;

    &.cart-value {
      color: rgba(0, 0, 0, 0.85);
    }

    &.selection-value {
      font-weight: 600;
      font-size: 1.4rem;
      color: rgba(0, 0, 0, 0.85);
    }
  }
}

.product-selection-error {
  color: rgba(255, 0, 0, 0.7);
}

.sidebar-limiter {
  padding: 16px 16px 16px 16px;
}

.side-container p {
  font-size: 1.3rem;
  margin: 0;
  padding-bottom: 14px;
  font-family: Arial, Helvetica, sans-serif;
  color: #000000a9;
}

.side-container ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 8px;
  font-size: 1.2rem;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: #000000a9;
  border-bottom: 1px solid #00000014;
}

.side-container ul:last-of-type {
  padding-bottom: 10px;
}

.side-container label {
  display: flex; /* Align the label content horizontally */
  align-items: center; /* Align the label content vertically */
  cursor: pointer;
}

.side-container input[type="checkbox"] {
  margin: 0;
  margin-right: 6px;
}
.checkbox-label {
  display: block;
}

.checkbox-input {
  filter: grayscale(100%) !important;
}

input[type="text"] {
  box-sizing: border-box;
  margin-bottom: 10px;
  padding: 5px;
  border: 0;
  border-bottom: 1px solid #0000006c;
  background-color: transparent;
  background-image: url("../../assets/images//search-icon-logo.png");
  background-size: 15px 15px;
  background-position: 1% center;
  background-repeat: no-repeat;
  padding-left: 25px;
  color: #000000ba;
}

input[type="text"]:focus {
  outline: none;
}

input[type="text"]::placeholder {
  color: #999;
  font-size: 1.2rem;
}

.showButton {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  padding: 10px 20px;
  border: none;
  background-color: rgba(128, 128, 128, 0.076);
  color: #000000ac;
  border-radius: 5px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.showButton:hover {
  background-color: rgba(128, 128, 128, 0.108);
}

.filter-slide-container {
  display: none;
}

.products-container {
  display: flex;
  flex-wrap: wrap;
  padding: 0 20px;
  gap: 1px;
  margin-bottom: 20px;
  width: 100%;

  &.fav {
    margin-left: 20%;
    width: 60%;
  }

  &.disabled {
    pointer-events: none;
  }
  //father container the Flex , give it 100% and children 25% for
  &.cart-container {
    border: 1px solid rgba(0, 0, 0, 0.1);
    width: 70%;
  }
}

.product-card {
  display: flex;
  flex-direction: column;
  position: relative;
  width: calc(25% - 2px);

  &.wide {
    width: calc(50% - 1px);

    .product-fav-icon {
      left: 93%;
      top: 1%;
    }
  }

  .product-card-wrapper {
    display: flex;
  }

  &.my-cart {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 30px 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
}

.product-img {
  width: 100%;
  object-fit: cover;
  object-position: center 5%;

  &.wide {
    width: 100%;
    height: 820px;
    object-position: center;
  }

  &.my-cart {
    width: 110px;
    margin-right: 20px;
  }
}

.product-fav-icon {
  position: absolute;
  top: 2%;
  left: 87%;
  padding: 5px;
  border-radius: 50%;
  background-color: #fff;
  cursor: pointer;

  &.my-fav {
    background-color: transparent;
    top: 0.1%;
    width: 8%;
    height: 6%;
  }
}

.product-title {
  font-family: "Mulish", sans-serif;
  color: #22242a;
  margin: 0;
  font-size: 1.2rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  &.cart-title {
    font-size: 1.4rem;
    white-space: nowrap;
  }
}

.product-price,
.product-salesprice,
.product-salesprice-value {
  font-family: "Mulish", sans-serif;
  margin: 0;
  font-size: 1.1rem;
}

.product-title {
  color: #22242a;

  &.item {
    font-size: 1.6rem;
  }
}

.product-price {
  font-size: 1.6rem;
  font-weight: 600;
  color: #212529;
  &.cart-price {
    font-family: "Mullish", sans-serif;
    font-weight: 800;
    color: #212529;
    font-size: 1.5rem;
  }

  &.shop {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 1200px) {
  .toggler-container {
    margin: 20px auto;
    max-width: 1200px;
  }

  span {
    display: none;
  }

  .productLayout {
    display: flex;
    margin: 0 auto;
    max-width: 1200px;
  }

  .products-container {
    display: flex;
    flex-wrap: wrap;
    padding: 0 20px;
    gap: 1px;
    margin-bottom: 20px;
    width: 100%;

    &.fav {
      margin: 0 auto;
      width: 60%;
    }

    &.cart-container {
      border: 1px solid rgba(0, 0, 0, 0.1);
      width: 70%;
      margin: 0 auto;
    }

    &.disabled {
      pointer-events: none;
    }
  }

  @media screen and (max-width: 1200px) {
    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background-color: white;
      z-index: 9999999;
      height: 1100px;
    }

    .overlay-header-container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 15px;
      position: relative;

      .overlay-title {
        font-family: "Mulish", sans-serif;
        font-size: 2rem;
        color: black;
        text-align: center;
        flex-grow: 1;
      }

      .overlay-button {
        font-size: 2rem;
        color: rgba(0, 0, 0, 0.734);
      }
    }
    .sort {
      position: absolute;
      right: 8%;
      top: 5%;
    }

    .overlay-sort {
      padding: 15px;
    }

    .sort-overlay {
      opacity: 0.7;
    }

    .sort-overlayer {
      position: absolute;
      padding: 10px 40px;
      margin: 0 auto;
      width: auto;
      border-radius: 10px;
      height: auto;
      z-index: 999999999;
      top: 45%;
      left: 30%;
      background-color: rgba(255, 255, 255, 0.913);
    }

    .sort-text {
      display: none;
    }

    .sortup {
      display: none;
    }

    .toggler-container {
      width: 100%;
      padding: 20px;
      margin: 0 auto;

      &.hide {
        display: none;
      }

      .toggler-wrapper {
        display: flex;
        justify-content: center;
        gap: 120px;

        .sort-Icon {
          width: 17px;
          height: 17px;
          display: inline-block;
          cursor: pointer;
        }

        .filter-Icon {
          width: 23px;
          height: 23px;

          cursor: pointer;
        }
      }

          .selection-filter-container ,
      .selection-filter{
        display: none;
        }
    }

    .productLayout {
      position: relative;
      z-index: 1;
      flex-direction: column;
      margin: 0 auto;

      &.hide {
        display: none;
      }
    }

    .productlayout-container {
      flex-direction: column;
    }

    .products-container {
      &.fav,
      &.cart-container {
        width: 100%;
        flex-direction: row;
        margin-top: 10px;
      }
    }

    .sidebar-container {
      width: 100%;
      display: none;
    }

    .product-card {
      width: calc(50% - 2px);

      &.wide {
        width: calc(50% - 1px);

        .product-fav-icon {
          left: 83%;
          top: 2%;
          width: 25px;
          height: 25px;
        }
      }
      .product-fav-icon {
        left: 83%;
        top: 2%;
        width: 25px;
        height: 25px;
      }

      .product-details-container {
        width: 190px;
      }

      .product-img.wide {
        width: 100%;
        height: 520px;
      }

      &.my-cart {
        padding: 20px 5px;
      }
    }

    .showButton {
      width: 100%;
    }

    .product-card.wide {
      width: calc(50% - 1px);
      .product-img.wide {
        width: 100%;
        height: 290px;
      }
    }

    .filter-slide-container {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: 1000;
      overflow: hidden;
    }

    .filter-slide-content {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 70%; /* Adjust the width as needed */
      background-color: #fff;
      overflow-y: auto;
      padding: 20px;
      box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
      transition: transform 0.3s ease-in-out;
    }

    .filter-slide-container.open {
      display: block;
    }

    .filter-slide-container .close-icon {
      position: absolute;
      top: 10px;
      right: 10px;
      cursor: pointer;
    }
  }

  .product-shopping-container {
    width: 100%;
    margin-bottom: 20px;
  }
}
